.tel-list {
  display: flex;
  flex-direction: column;
  border: none;
  padding: 0;
  margin: 0;
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    margin-bottom: 24px;
  }
}

.tel-list__title {
  margin-bottom: 3px;

  @media (min-width: $tablet-width) {
    margin-top: 17px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    top: 0;
    left: -114px;
  }
}

.tel-list__item {
  position: relative;
  border: none;
  border-bottom: 2px solid $basic-black;
  width: 260px;

  @media (min-width: $tablet-width) {
    width: 100%;
    padding-bottom: 7px;
    padding-left: 15px;
  }

  @media (min-width: $desktop-width) {
    width: 700px;
  }
}

.tel-list__title-icon {
  position: absolute;
  width: 25px;
  height: 24px;
  top: 30px;
  right: 7px;
  z-index: 1;
  fill: $basic-black;
  opacity: 0.3;

  &:hover {
    fill: $tiffany;
    opacity: 1;
  }

  @media (min-width: $tablet-width) {
    top: 11px;
    left: 618px;
  }

  @media (min-width: $desktop-width) {
    top: 11px;
    left: 668px;
  }
}

.tel-list__item:hover + .tel-list__title-icon {
  fill: $tiffany;
  opacity: 1;
}
